* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
}

body {
  background: rgb(8, 8, 8);
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 370px;
  min-height: 600px;
  background: #111d52;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

.toDo-form {
  margin-bottom: 32px;
}

.toDo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  margin: 10px;
  outline: none;
  width: 78%;
  background: transparent;
  color: #fff;
}

.toDo-input::placeholder {
  color: #e2e2e2;
}

.toDo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
          90deg,
          rgba(93, 12, 255, 1) 0%,
          rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
  text-transform: capitalize;
}

.toDo-input.edit {
  border: 2px solid #149fff;
}

.toDo-button.edit {
  background: linear-gradient(
          90deg,
          rgba(20, 159, 255, 1) 0%,
          rgba(17, 122, 255, 1) 100%
  );
  padding: 16px 22px;
}

.toDo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.toDo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(
          90deg,
          rgba(255, 118, 20, 1) 0%,
          rgba(255, 84, 17, 1) 100%
  );

  padding: 14px;
  border-radius: 5px;
  width: 78%;
}

.toDo-row:nth-child(4n + 1) {
  background: linear-gradient(
          90deg,
          rgba(93, 12, 255, 1) 0%,
          rgba(155, 0, 250, 1) 100%
  );
}

.toDo-row:nth-child(4n + 2) {
  background: linear-gradient(
          90deg,
          rgba(255, 12, 241, 1) 0%,
          rgba(250, 0, 135, 1) 100%
  );
}

.toDo-row:nth-child(4n + 3) {
  background: linear-gradient(
          90deg,
          rgba(20, 159, 255, 1) 0%,
          rgba(17, 122, 255, 1) 100%
  );
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}